.home {
  min-height: 100vh;
}

/* Enhanced Hero Section */
.hero {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 100px 0;
  display: flex;
  align-items: center;
  min-height: 90vh;
  position: relative;
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grain" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="50" cy="50" r="1" fill="white" opacity="0.1"/></pattern></defs><rect width="100" height="100" fill="url(%23grain)"/></svg>');
  opacity: 0.3;
}

.hero-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 80px;
  align-items: center;
  position: relative;
  z-index: 2;
}

.hero-content h1 {
  font-size: 4rem;
  font-weight: 800;
  margin-bottom: 25px;
  line-height: 1.1;
  background: linear-gradient(135deg, #ffffff 0%, #f0f0f0 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: slideInLeft 1s ease-out;
}

.hero-content p {
  font-size: 1.3rem;
  margin-bottom: 35px;
  opacity: 0.95;
  line-height: 1.7;
  animation: slideInLeft 1s ease-out 0.2s both;
}

.hero-buttons {
  display: flex;
  gap: 25px;
  flex-wrap: wrap;
  animation: slideInLeft 1s ease-out 0.4s both;
}

.hero-image {
  text-align: center;
  animation: slideInRight 1s ease-out 0.6s both;
}

.hero-image img {
  max-width: 100%;
  height: auto;
  border-radius: 24px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
}

.hero-image img:hover {
  transform: scale(1.05);
  box-shadow: 0 35px 70px rgba(0, 0, 0, 0.4);
}

/* Enhanced Features Section */
.features {
  padding: 100px 0;
  background: white;
  position: relative;
}

.features::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(102, 126, 234, 0.3), transparent);
}

.features h2 {
  text-align: center;
  font-size: 3rem;
  font-weight: 800;
  margin-bottom: 80px;
  color: #333;
  background: linear-gradient(135deg, #333 0%, #667eea 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 50px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.feature {
  text-align: center;
  padding: 40px 30px;
  border-radius: 16px;
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid rgba(102, 126, 234, 0.1);
  position: relative;
  overflow: hidden;
}

.feature::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.feature:hover::before {
  transform: scaleX(1);
}

.feature:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  border-color: rgba(102, 126, 234, 0.3);
}

.feature-icon {
  font-size: 3.5rem;
  margin-bottom: 25px;
  display: inline-block;
  transition: all 0.3s ease;
}

.feature:hover .feature-icon {
  transform: scale(1.1);
}

.feature h3 {
  font-size: 1.4rem;
  font-weight: 700;
  margin-bottom: 18px;
  color: #333;
}

.feature p {
  color: #666;
  line-height: 1.7;
  font-size: 1rem;
}

/* Enhanced Services Section */
.services {
  padding: 100px 0;
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
  position: relative;
}

.services::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(102, 126, 234, 0.3), transparent);
}

.services h2 {
  text-align: center;
  font-size: 3rem;
  font-weight: 800;
  margin-bottom: 80px;
  color: #333;
  background: linear-gradient(135deg, #333 0%, #667eea 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.service-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.services-cta {
  text-align: center;
  margin-top: 70px;
}

/* Enhanced CTA Section */
.cta {
  padding: 100px 0;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.cta::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grain" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="50" cy="50" r="1" fill="white" opacity="0.1"/></pattern></defs><rect width="100" height="100" fill="url(%23grain)"/></svg>');
  opacity: 0.3;
}

.cta h2 {
  font-size: 3rem;
  font-weight: 800;
  margin-bottom: 25px;
  position: relative;
  z-index: 2;
}

.cta p {
  font-size: 1.3rem;
  margin-bottom: 35px;
  opacity: 0.95;
  position: relative;
  z-index: 2;
}

/* Enhanced Responsive Design */
@media (max-width: 768px) {
  .hero {
    padding: 80px 0;
    min-height: 80vh;
  }

  .hero-content {
    grid-template-columns: 1fr;
    gap: 50px;
    text-align: center;
  }

  .hero-content h1 {
    font-size: 3rem;
  }

  .hero-content p {
    font-size: 1.1rem;
  }

  .hero-buttons {
    justify-content: center;
  }

  .features h2,
  .services h2,
  .cta h2 {
    font-size: 2.5rem;
  }

  .features-grid {
    grid-template-columns: 1fr;
    gap: 40px;
  }

  .service-grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .feature {
    padding: 30px 25px;
  }

  .feature-icon {
    font-size: 3rem;
  }

  .features,
  .services {
    padding: 80px 0;
  }
}

@media (max-width: 480px) {
  .hero-content h1 {
    font-size: 2.5rem;
  }

  .hero-content p {
    font-size: 1rem;
  }

  .hero-buttons {
    flex-direction: column;
    align-items: center;
  }

  .features h2,
  .services h2,
  .cta h2 {
    font-size: 2rem;
  }

  .hero {
    padding: 60px 0;
  }

  .features,
  .services {
    padding: 60px 0;
  }
} 