.services-page {
  min-height: 100vh;
}

.services-hero {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 100px 0;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.services-hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grain" width="100" height="100" patternUnits="userSpaceOnUse"><circle cx="50" cy="50" r="1" fill="white" opacity="0.1"/></pattern></defs><rect width="100" height="100" fill="url(%23grain)"/></svg>');
  opacity: 0.3;
}

.services-hero h1 {
  font-size: 3.5rem;
  font-weight: 800;
  margin-bottom: 25px;
  position: relative;
  z-index: 2;
  background: linear-gradient(135deg, #ffffff 0%, #f0f0f0 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.services-hero p {
  font-size: 1.3rem;
  opacity: 0.95;
  max-width: 600px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
  line-height: 1.7;
}

.services-content {
  padding: 100px 0;
  background: linear-gradient(135deg, #f8f9fa 0%, #ffffff 100%);
  position: relative;
}

.services-content::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(102, 126, 234, 0.3), transparent);
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
}

.service-item {
  position: relative;
  transition: all 0.3s ease;
}

.service-item:hover {
  transform: translateY(-5px);
}

.service-price {
  position: absolute;
  top: 25px;
  right: 25px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  padding: 10px 20px;
  border-radius: 25px;
  font-size: 15px;
  font-weight: 700;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
  z-index: 10;
  transition: all 0.3s ease;
}

.service-item:hover .service-price {
  transform: scale(1.05);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

.services-cta {
  padding: 100px 0;
  background: white;
  text-align: center;
  position: relative;
}

.services-cta::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(102, 126, 234, 0.3), transparent);
}

.services-cta h2 {
  font-size: 3rem;
  font-weight: 800;
  margin-bottom: 25px;
  color: #333;
  background: linear-gradient(135deg, #333 0%, #667eea 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.services-cta p {
  font-size: 1.2rem;
  color: #666;
  margin-bottom: 35px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.7;
}

/* Enhanced Responsive Design */
@media (max-width: 768px) {
  .services-hero h1 {
    font-size: 3rem;
  }
  
  .services-hero p {
    font-size: 1.1rem;
  }
  
  .services-grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }
  
  .services-cta h2 {
    font-size: 2.5rem;
  }

  .services-content {
    padding: 80px 0;
  }

  .services-cta {
    padding: 80px 0;
  }
}

@media (max-width: 480px) {
  .services-hero h1 {
    font-size: 2.5rem;
  }
  
  .services-cta h2 {
    font-size: 2rem;
  }

  .services-content {
    padding: 60px 0;
  }

  .services-cta {
    padding: 60px 0;
  }

  .service-price {
    top: 20px;
    right: 20px;
    padding: 8px 16px;
    font-size: 14px;
  }
} 